@use 'src/sass/mixins/breakpoint' as *;

.container {
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 32px;

  @include media-query(mobile) {
    padding: 0 20px;
  }

  @include media-query(tablet) {
    max-width: 768px;
  }

  @include media-query(desktop) {
    max-width: 1280px;
  }
}
